import http from '../axios';

const endpoint = '/sync-logs';

export const fetch = () => http.get(endpoint);

export const fetchSyncLog = uuid => http.get(`${endpoint}/${uuid}`);

export const patchSyncLog = (uuid, body) => http.patch(`${endpoint}/${uuid}`, body);

export const deleteSyncLog = (uuid) => http.delete(`${endpoint}/${uuid}`);
