<template>
  <v-card outlined>
    <v-card-title>
      <v-btn @click="onBack" icon medium class="mr-1">
        <v-icon medium> fa-angle-left </v-icon>
      </v-btn>
      <span>Sync Logs</span>
    </v-card-title>
    <v-card-text>
      <v-row no-gutter>
        <v-col cols="12" lg="12" xl="12" md="12" sm="12" xs="12">
          <generic-grid
            :creatable="false"
            :deletable="false"
            :hideActions="true"
            :service="service"
            :gridName="gridName"
            :headers="gridHeaders"
            :itemRowBackground="itemRowBackground"
            itemKey="global_uuid"
            :toolbarTitle="toolbarTitle"
            :extraConfig="extraConfig"
            :isHeaderConfigurable="true"
          >
            <template v-slot:gridItemActions="{ item, refreshGrid }">
              <v-icon small @click="toggleDialog(dialogNames.syncLogs, item, refreshGrid)" color="primary">fa-pencil</v-icon>
              <v-icon small @click="clickDelete(item)" color="error">fa-trash-o</v-icon>
            </template>
          </generic-grid>
        </v-col>
      </v-row>
    </v-card-text>
    <v-dialog persistent max-width="500px" v-if="selectedItem" v-model="dialog[dialogNames.syncLogs]">
      <sync-log-form
        v-if="dialog[dialogNames.syncLogs]"
        :refreshGrid="refreshGridCb"
        :onCancel="() => closeDialog(dialogNames.syncLogs)"
        :item="selectedItem"
      ></sync-log-form>
    </v-dialog>
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      :loading="loadingDelete"
      dialog-text="Do you want to delete?"
    ></confirm-dialog>
  </v-card>
</template>
<script>
import SyncLogForm from './syncLogForm.vue';
import IconButton from '../Common/IconButton.vue';
import GenericGrid from '../Common/GenericGrid.vue';
import * as gridNames from '../../constants/gridNames';
import * as syncLogsService from '../../services/syncLogsServices';
import ConfirmDialog from '../Common/ConfirmDialog';
import * as syncLogService from "frontend/src/services/syncLogsServices";

const dialogNames = {
  syncLogs: 'syncLogs',
};

export default {
  name: 'sync-logs',
  components: { ConfirmDialog, IconButton, GenericGrid, SyncLogForm },
  data: () => ({
    service: syncLogsService,
    refreshGridCb: f => f,
    selectedItem: null,
    toolbarTitle: 'Sync Logs',
    gridName: gridNames.SYNC_LOGS_GRID,
    extraConfig: {
      hasActions: false,
    },
    gridHeaders: [
      {
        value: 'global_uuid',
        text: 'Global UUID',
        align: 'center',
        sortable: false,
      },
      {
        value: 'last_sync_id_to_envizi',
        text: 'Last Sync Id to Envizi',
        sortable: true,
        align: 'center',
      },
      {
        value: 'last_sync_datetime_to_envizi',
        text: 'Last Sync DateTime to Envizi',
        sortable: true,
        align: 'center',
      },
      {
        value: 'latest_sync_id_in_postgres',
        text: 'Latest Sync Id in Postgres',
        sortable: true,
        align: 'center',
      },
      {
        value: 'latest_sync_datetime_in_postgres',
        text: 'Latest Sync DateTime in Postgres',
        sortable: true,
        align: 'center',
      },
      {
        value: 'max_sync_id_in_postgres',
        text: 'Max Sync Id in Postgres',
        sortable: true,
        align: 'center',
      },
      {
        value: 'has_envizi_record',
        text: 'Has Envizi Record',
        sortable: true,
        align: 'center',
      },
      {
        value: 'has_postgres_record',
        text: 'Has Postgres Record',
        sortable: true,
        align: 'center',
      },
      {
        value: 'gridItemActions',
        text: 'Actions',
        align: 'center',
      },
    ],
    dialogNames: dialogNames,
    dialog: {
      [dialogNames.syncLogs]: false,
    },
    loadingDelete: false,
    dialogDelete: false,
  }),
  computed: {},
  methods: {
    onBack() {
      this.$router.go(-1);
    },
    itemRowBackground(item) {
      const errorCondition = (Number(item.last_sync_id_to_envizi) > Number(item.max_sync_id_to_postgres) ||
        Number(item.last_sync_id_to_envizi) > Number(item.latest_sync_id_in_postgres))
      return errorCondition &&  Number(item.latest_sync_id_in_postgres) === 0 ?
        'background-yellow' : errorCondition &&  Number(item.latest_sync_id_in_postgres) !== 0  ?
          'background-red': '';
    },
    toggleDialog(dialogName, item, refreshGrid) {
      this.selectedItem = item;
      this.dialog[dialogName] = !this.dialog[dialogName];
      this.refreshGridCb = refreshGrid;
      if (this.dialog[dialogName]) {
        this.selectedItem = item;
      } else {
        this.selectedItem = null;
      }
    },
    closeDialog(dialogName) {
      this.dialog[dialogName] = false;
      this.selectedItem = {};
    },
    clickDelete(item) {
      this.dialogDelete = true;
      this.selectedItem = item;
    },
    async confirm() {
      this.loadingDelete = true;
      try {
        const response = await syncLogService.deleteSyncLog(this.selectedItem.global_uuid);
        if ((response.notif = 'Success')) {
          this.$toasted.show('Deleted successfully!');
        }
      } finally {
        this.loadingDelete = false;
        this.dialogDelete = false;
      }
    },
    closeDelete() {
      this.dialogDelete = false;
    },
  },
};
</script>
